async function fetchExpansePayments({commit}, params) {
    return await this.$axios
        .get('/expense-payments', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_EXPENSES", res.data);
            }

            return res.data
        }).catch((error) => {
            return error
        })
}

