/* eslint-disable */
import {ability} from '@/plugins/abilities.js'

async function getFilteredData({commit}, payload) {
    return await this.$axios.get(payload.routeURL + '?search=' + payload.searchValue, )
        .then((res) => {
            if (res && res.data) {
            }
            return res.data
        }).catch((error) => {
            return error
        })
}


async function fetchRoles({commit, state}, payload) {
  return await this.$axios({
      method: "GET",
      url: '/roles',
    })
    .then(async (e) => {
      await commit('SET_ROLES', e.data)
      return e;
    })
    .catch((error) => {
      return error;
    })
}


async function fetchUsers({commit, state}, payload) {
  return await this.$axios({
      method: "GET",
      url: '/users?page=' + payload.page,
    })
    .then(async (e) => {
      await commit('SET_USERS', e.data)
      return e;
    })
    .catch((error) => {
      return error;
    })
}


async function fetchOneUser({commit}, payload) {
  return await this.$axios({
      method: "GET",
      url: '/users/' + payload,
    })
    .then(async (e) => {
      await commit('SET_ONE_USER', e.data)
      return e;
    })
    .catch((error) => {
      return error;
    })
}


async function deleteUser({commit}, payload) {
  return await this.$axios({
      method: "DELETE",
      url: `/users/${payload}`,
    })
    .then(async (e) => {
      return e;
    })
    .catch((error) => {
      return error;
    })
}


async function postUsers({commit}, payload) {
  return await this.$axios({
      method: "POST",
      url: '/users',
      data: payload
    })
    .then(async (e) => {
      return e;
    })
    .catch((error) => {
      return error;
    })
}

async function updateUsers({commit}, payload) {
  return await this.$axios({
      method: "PUT",
      url: '/users/' + payload.id,
      data: payload
    })
    .then(async (e) => {
      return e;
    })
    .catch((error) => {
      return error;
    })
}

async function updateUserLogin({commit}, payload) {
  return await this.$axios({
      method: "POST",
      url: '/update-login',
      data: payload
    })
    .then(async (e) => {
      return e;
    })
    .catch((error) => {
      return error;
    })
}


async function updateUserPhoto({commit}, payload) {
  return await this.$axios({
      method: "POST",
      url: `/image-upload?id=${payload.id}`,
      headers: {'Content-Type': 'multipart/form-data'},
      data: payload.image
    })
    .then(async (e) => {
      return e;
    })
    .catch((error) => {
      return error;
    })
}


async function updateUserPassword({commit}, payload) {
  return await this.$axios({
      method: "POST",
      url: '/update-password',
      data: payload
    })
    .then(async (e) => {
      return e;
    })
    .catch((error) => {
      return error;
    })
}


async function fetchPermissions({commit, state}, payload) {
  return await this.$axios({
      method: "GET",
      url: '/permissions?name=' + payload,
    })
    .then(async (e) => {
      await commit('SET_USER_PERMISSIONS', e.data)
      sessionStorage.setItem('permissions', JSON.stringify(e.data));
      ability.update(state.permissions)
      return e;
    })
    .catch((error) => {
      return error;
    })
}

function stateChanged({commit}, payload){
  commit('SET_USER_TO_STATE', payload.user);
  commit('SET_USER_PERMISSIONS', payload.permissions)
  ability.update(payload.permissions)
}



async function fetchPermissionsOfUsers({commit, state}, payload) {
  return await this.$axios({
      method: "GET",
      url: '/permissions?name=' + payload,
    })
    .then(async (e) => {
      return e.data;
    })
    .catch((error) => {
      return error;
    })
}


async function updatePermissions({commit}, payload) {
  return await this.$axios({
      method: "PUT",
      url: '/permissions/' + payload.id,
      data: {
        permissions: payload.permissions
      }
    })
    .then((e) => {
      return e;
    })
    .catch((error) => {
      return error;
    })
}

async function postSignIn({commit}, payload) {
  return await this.$axios({
      method: "POST",
      url: '/login',
      data: {
        email: payload.email,
        password: payload.password
      },
    })
    .then((e) => {
      commit('SET_USER_TO_STATE', e.data);
      sessionStorage.setItem('user', JSON.stringify(e.data));
      // payload.router.push({path: '/main'});
      return e;
    })
    .catch((error) => {
      return error;
    })
}



async function LOG_OUT({commit}, payload) {
  return await this.$axios({
      method: "GET",
      url: '/logout',
    })
    .then((e) => {
      commit('LOG_OUT_MUTATION');
      return e;
    })
    .catch((error) => {
      return error;
    })
}





export default { getFilteredData, postSignIn, stateChanged, fetchRoles,
  updatePermissions, fetchPermissions, fetchUsers, postUsers, updateUserLogin,
  deleteUser, fetchOneUser, updateUserPhoto, updateUserPassword, LOG_OUT,
  fetchPermissionsOfUsers, updateUsers };
