/* eslint-disable */

async function updateFactoryIncome({commit}, payload) {
    return await this.$axios({
        method: "PUT",
        url: '/factory-income/' + payload.id,
        data: payload
    })
    .then(async (e) => {
        return e;
    })
    .catch((error) => {
        return error;
    })
}

async function deleteFactoryIncome({commit}, payload) {
    return await this.$axios({
        method: "DELETE",
        url: '/factory-income/' + payload,
        data: {
            factory_product_case: 'raw',
            storage_product_case: 'raw',
            storage_type_id: '1'
        }
    })
    .then(async (e) => {
        return e;
    })
    .catch((error) => {
        return error;
    })
}


