async function fetchClients({commit}, params) {
    return await this.$axios
        .get('/clients', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_CLIENTS", res.data);
            }

            return res.data
        }).catch((error) => {
            return error
        })
}


async function postClient({commit}, payload) {
    return await this.$axios({
        method: "POST",
        url: '/clients',
        data: payload
    })
    .then(async (e) => {
        return e.data;
    })
    .catch((error) => {
        return error;
    })
}



export default {fetchReadyOrderIncome, fetchSales, postSales,
     fetchStorageReadyProduct, fetchClients, postClient};
